body {
  margin: 0;
  padding: 0;
  color: #414141;
  font-family: 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1,
h2,
h3,
h4,
h5 {
  font-family: 'ADAMCGPRO', 'HelveticaNeue-Bold', 'Helvetica Neue', sans-serif;
}

h1,
h2 {
  color: #fff;
}

h1 {
  font-size: 45px;
}

h2 {
  font-size: 30px;
}

h3 {
  color: #6a45b4;
  font-size: 35px;
  margin-bottom: 20px;
}

.skills-container {
  margin-top: 400px;
  margin-bottom: 400px;
}

.skills-container ul {
  font-size: 18px;
  line-height: 30px;
}

ul {
  padding-left: 20px;
  list-style-type: none;
}

li:before {
  display: inline-block;
  content: '//';
  margin-left: -17px;
  margin-right: 8px;
}

li {
  margin-bottom: 5px;
}

.my-work-container {
  margin-bottom: 400px;
}

@media all and (max-width: 770px) {
  h1 {
    font-size: 40px;
  }

  h2 {
    font-size: 25px;
  }

  .skills-container {
    margin-top: 200px;
  }

  .skills-container h3 {
    margin-bottom: 5px;
  }

  .skills-container .col-one {
    margin-bottom: 100px;
  }

  .skills-container,
  .my-work-container {
    margin-bottom: 200px;
  }
}
