.hero-container {
  height: 100vh;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url('../../assets/images/hero-image.jpg');
}

header.hero {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 25px;
}

.top-hero {
  border-bottom: 1px #fff solid;
  padding-bottom: 25px;
  margin-bottom: 35px;
}

.hero-container a,
.hero-container a:visited {
  color: #fff;
  text-decoration: underline;
}

.hero-container a:hover {
  text-decoration: underline;
}

.download-resume-icon {
  margin-top: -3px;
}

@media all and (max-width: 1150px) and (max-height: 700px) {
  header.hero h1 {
    font-size: 32px;
  }
}

@media all and (max-width: 870px) and (max-height: 480px) {
  header.hero h1 {
    font-size: 28px;
  }

  .top-hero {
    padding-bottom: 15px;
    margin-bottom: 25px;
  }

  header.hero h2 {
    font-size: 25px;
  }
}

@media all and (max-height: 450px) {
  .hero-container {
    background-size: 150%;
  }
}
